body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}





body{
  /* background-image: url('./source/background.png') */
  /* background-image: linear-gradient(#473253, #243e5f); */
  background-color: #252627;
}
a{
  text-decoration: none;
}
li{
  list-style: none;
}
ul{
  margin: 0;
  padding: 0;
}
div{
  margin: 0;
  padding: 0;
}

.wrap{
  display: flex;
}

.left-menu{
  display: flex;
  width: 50px;
  min-height: 100vh;
  background-color: #181818;
  flex-direction: column;
  justify-content: space-between;
  
}
.left-menu_header{
 
  text-align: center;
  padding: 10px 0;
}
.left-menu_logo{
  color: #08fdd8;
  font-weight: bolder;
  font-size: 48px;
  text-shadow:2px 0px 3px #eee;
  -webkit-transition: all .25 ease-out;
  transition: all .25 ease-out;
  margin: 0;
}
.left-menu_logo:hover{
  -webkit-animation: logoAnimation 0.8s ease-in-out  1;
          animation: logoAnimation 0.8s ease-in-out  1;
}
.left-menu_logo__desc{
  color: #fff;
  font-size: 12px;
}

.left-menu_content{
  justify-content: flex-start;

}
.left-menu_content__elem{
  text-align: center;
  color: #4d4d4e;
  font-size: 25px;

}
.left-menu_content__elem_active{
  text-align: center;
  color: #179180;
  font-size: 25px;
}
.left-menu_footer{
    text-align: center;
    padding-bottom: 10px;
}
.social-link{
  color: #4d4d4d;
  font-size: 16px;
}
.social-link:hover{
  color: #179180;
}

@-webkit-keyframes logoAnimation {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
    
    10%,
    20% {
    -webkit-transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg)
    }
    
    30%,
    50%,
    70%,
    90% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg)
    }
    
    40%,
    60%,
    80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg)
    }
    
    100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
}

@keyframes logoAnimation {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
    
    10%,
    20% {
    -webkit-transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(.9, .9, .9) rotate3d(0, 0, 1, -3deg)
    }
    
    30%,
    50%,
    70%,
    90% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, 3deg)
    }
    
    40%,
    60%,
    80% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg);
    transform: scale3d(1.1, 1.1, 1.1) rotate3d(0, 0, 1, -3deg)
    }
    
    100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1)
    }
}
.blast {
  color: aliceblue;
  font-size: 50px;
  display: inline-block;
  font-weight: bolder;
}
.blast:hover {
  -webkit-animation: textAnimation .6s ease-in-out 1;
          animation: textAnimation .6s ease-in-out 1;
  color: #08fdd8;
}

@-webkit-keyframes textAnimation {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  30% {
    -webkit-transform: scale3d(1.25, 0.75, 1);
    transform: scale3d(1.25, 0.75, 1);
  }

  40% {
    -webkit-transform: scale3d(0.75, 1.25, 1);
    transform: scale3d(0.75, 1.25, 1);
  }

  50% {
    -webkit-transform: scale3d(1.15, 0.85, 1);
    transform: scale3d(1.15, 0.85, 1);
  }

  65% {
    -webkit-transform: scale3d(0.95, 1.05, 1);
    transform: scale3d(0.95, 1.05, 1);
  }

  75% {
    -webkit-transform: scale3d(1.05, 0.95, 1);
    transform: scale3d(1.05, 0.95, 1);
  }

  100% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}

@keyframes textAnimation {
  0% {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }

  30% {
